import axios from 'axios';

export const baseUrl = 'http://neteasecloudmusicapi.zhaoboy.com';
export const prod_base_url = '/api'
//axios 
const axiosInstance = axios.create({
  baseURL: baseUrl
});

axiosInstance.interceptors.response.use(
  res => res.data,
  err => {
    console.log(err, "");
  }
);

export {
  axiosInstance
};

