import React, { useRef, useState, useEffect } from "react";
import { connect } from "react-redux";
import {
    changePlayingState,
    changeShowPlayList,
    changeCurrentIndex,
    changeCurrentSong,
    changePlayList,
    changePlayMode,
    changeFullScreen
} from "./store/actionCreators";
import MiniPlayer from './miniPlayer';
import NormalPlayer from './normalPlayer';
import { getSongUrl, isEmptyObject, shuffle, findIndex } from "../../api/utils";
import { playMode } from '../../api/config';
import Toast from "./../../baseUI/Toast";
import PlayList from "./play-list"
import { getLyricRequest } from '../../api/request'
import Lyric from './../../api/lyric-parser';

function Player(props) {
    const {
        fullScreen,
        playing,
        currentIndex,
        playList: immutablePlayList,
        currentSong: immutableCurrentSong,
        mode,//
        sequencePlayList: immutableSequencePlayList,// 
    } = props;
    const {
        togglePlayingDispatch,
        changeCurrentIndexDispatch,
        changeCurrentDispatch,
        changePlayListDispatch,//playList
        changeModeDispatch,//mode
        toggleFullScreenDispatch,
        togglePlayListDispatch
    } = props;

    const playList = immutablePlayList.toJS();
    const sequencePlayList = immutableSequencePlayList.toJS();
    const currentSong = immutableCurrentSong.toJS();

    // 
    const [currentPlayingLyric, setPlayingLyric] = useState("");
    const currentLyric = useRef();
    //  currentLineNum:
    const currentLineNum = useRef(0);

    // audio
    const songReady = useRef(true);
    const [modeText, setModeText] = useState("");
    // 
    const toastRef = useRef();
    //
    const [preSong, setPreSong] = useState({});
    //ref
    const audioRef = useRef();
    //
    const [currentTime, setCurrentTime] = useState(0);
    //
    const [duration, setDuration] = useState(0);
    //
    let percent = isNaN(currentTime / duration) ? 0 : currentTime / duration;

    //mockcurrentIndex
    // useEffect(() => {
    //     changeCurrentIndexDispatch(0);
    // }, [])

    useEffect(() => {
        if (
            !playList.length ||
            currentIndex === -1 ||
            !playList[currentIndex] ||
            playList[currentIndex].id === preSong.id
        )
            return;
        let current = playList[currentIndex];
        setPreSong(current);
        songReady.current = false; //  false, 
        changeCurrentDispatch(current);//currentSong
        audioRef.current.src = getSongUrl(current.id);
        setTimeout(() => {
            audioRef.current.play().then(() => {
                songReady.current = true;
            }, (err) => { console.log(err) });
        });
        togglePlayingDispatch(true);//
        getLyric(current.id);
        setCurrentTime(0);//
        setDuration((current.dt / 1000) | 0);//
    }, [playList, currentIndex]);


    useEffect(() => {
        playing ? audioRef.current.play() : audioRef.current.pause();
    }, [playing]);

    const clickPlaying = (e, state) => {
        e.stopPropagation();
        togglePlayingDispatch(state);
        // console.log('play')
        if (currentLyric.current) {
            currentLyric.current.togglePlay (currentTime*1000);
          }
    };

    const updateTime = e => {
        setCurrentTime(e.target.currentTime);
    };

    // 
    const onProgressChange = curPercent => {
        const newTime = curPercent * duration;
        setCurrentTime(newTime);
        audioRef.current.currentTime = newTime;
        if (!playing) {
            togglePlayingDispatch(true);
        }
        if (currentLyric.current) {
            currentLyric.current.seek (newTime * 1000);
          }
    };

    //
    const handleLoop = () => {
        audioRef.current.currentTime = 0;
        changePlayingState(true);
        audioRef.current.play();
    };

    const handlePrev = () => {
        //
        if (playList.length === 1) {
            handleLoop();
            return;
        }
        let index = currentIndex - 1;
        if (index < 0) index = playList.length - 1;
        if (!playing) togglePlayingDispatch(true);
        changeCurrentIndexDispatch(index);
    };

    const handleNext = () => {
        //
        if (playList.length === 1) {
            handleLoop();
            return;
        }
        let index = currentIndex + 1;
        if (index === playList.length) index = 0;
        if (!playing) togglePlayingDispatch(true);
        changeCurrentIndexDispatch(index);
    };

    const changeMode = () => {
        let newMode = (mode + 1) % 3;
        if (newMode === 0) {
            //
            changePlayListDispatch(sequencePlayList);
            let index = findIndex(currentSong, sequencePlayList);
            changeCurrentIndexDispatch(index);
            setModeText("");
        } else if (newMode === 1) {
            //
            changePlayListDispatch(sequencePlayList);
            setModeText("");
        } else if (newMode === 2) {
            //
            let newList = shuffle(sequencePlayList);
            let index = findIndex(currentSong, newList);
            changePlayListDispatch(newList);
            changeCurrentIndexDispatch(index);
            setModeText("");
        }
        changeModeDispatch(newMode);
        toastRef.current.show();
    };

    const handleEnd = () => {
        if (mode === playMode.loop) {
            handleLoop();
        } else {
            handleNext();
        }
    }

    const handleError = () => {
        songReady.current = true;
        console.log("");
    };

    const handleLyric = ({ lineNum, txt }) => {
        if (!currentLyric.current) return;
        currentLineNum.current = lineNum;
        setPlayingLyric(txt);
    };

    const getLyric = id => {
        let lyric = "";
        if (currentLyric.current) {
            currentLyric.current.stop();
        }
        //  songReady  false 
        getLyricRequest(id)
            .then(data => {
                lyric = data.lrc.lyric;
                if (!lyric) {
                    currentLyric.current = null;
                    return;
                }
                currentLyric.current = new Lyric(lyric, handleLyric);
                currentLyric.current.play();
                currentLineNum.current = 0;
                currentLyric.current.seek(0);
            })
            .catch(() => {
                songReady.current = true;
                audioRef.current.play();
            });
    };


    return (
        <div>
            { isEmptyObject(currentSong) ? null :
                <MiniPlayer
                    playing={playing}
                    song={currentSong}
                    fullScreen={fullScreen}
                    toggleFullScreen={toggleFullScreenDispatch}
                    clickPlaying={clickPlaying}
                    percent={percent}
                    togglePlayList={togglePlayListDispatch}
                />}
            {isEmptyObject(currentSong) ? null :
                <NormalPlayer
                    song={currentSong}
                    fullScreen={fullScreen}
                    toggleFullScreen={toggleFullScreenDispatch}
                    percent={percent}
                    playing={playing}
                    duration={duration}//
                    currentTime={currentTime}//
                    clickPlaying={clickPlaying}
                    onProgressChange={onProgressChange}
                    handlePrev={handlePrev}
                    handleNext={handleNext}
                    mode={mode}
                    changeMode={changeMode}
                    togglePlayList={togglePlayListDispatch}
                    currentLyric={currentLyric.current}
                    currentPlayingLyric={currentPlayingLyric}
                    currentLineNum={currentLineNum.current}
                />}
            <audio ref={audioRef} onTimeUpdate={updateTime} onEnded={handleEnd} onError={handleError}></audio>
            <PlayList></PlayList>
            <Toast text={modeText} ref={toastRef}></Toast>
        </div>
    )
}

//  Redux  state  props 
const mapStateToProps = state => ({
    fullScreen: state.getIn(["player", "fullScreen"]),
    playing: state.getIn(["player", "playing"]),
    currentSong: state.getIn(["player", "currentSong"]),
    showPlayList: state.getIn(["player", "showPlayList"]),
    mode: state.getIn(["player", "mode"]),
    currentIndex: state.getIn(["player", "currentIndex"]),
    playList: state.getIn(["player", "playList"]),
    sequencePlayList: state.getIn(["player", "sequencePlayList"])
});

//  dispatch  props 
const mapDispatchToProps = dispatch => {
    return {
        togglePlayingDispatch(data) {
            dispatch(changePlayingState(data));
        },
        toggleFullScreenDispatch(data) {
            dispatch(changeFullScreen(data));
        },
        togglePlayListDispatch(data) {
            dispatch(changeShowPlayList(data));
        },
        changeCurrentIndexDispatch(index) {
            dispatch(changeCurrentIndex(index));
        },
        changeCurrentDispatch(data) {
            dispatch(changeCurrentSong(data));
        },
        changeModeDispatch(data) {
            dispatch(changePlayMode(data));
        },
        changePlayListDispatch(data) {
            dispatch(changePlayList(data));
        }
    };
};

//  ui 
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(React.memo(Player));